import styles from "../styles/take_attendance";
import { ScrollView, SafeAreaView, View, Alert, LogBox } from "react-native";
import StudentBox from "../components/StudentBox";
import { faAngleDoubleRight, faTimes } from "@fortawesome/free-solid-svg-icons";
import colors from "../../config/colors";
import { useState, useEffect } from "react";
import Button from "../components/Button";
import AttendanceModal from "../components/AttendanceModal";

const Attendance = ({ navigation }) => {
  const [showModal, setShowModal] = useState(false);
  const [students, setStudents] = useState([]);
  const [presentStudents, setPresentStudents] = useState([]);

  useEffect(() => {
    // Dummy student data (9 present, 6 absent)
    const dummyStudents = [
      { access_no: "UG-202401", name: "Timothy Olimba", roll_no: "A96722", country: "Uganda" }, // Present
      { access_no: "UG-202402", name: "Aisha Nansubuga", roll_no: "A96543", country: "Uganda" }, // Present
      { access_no: "UG-202403", name: "Daniel Kato", roll_no: "A96321", country: "Uganda" }, // Present
      { access_no: "UG-202404", name: "Mercy Tumwine", roll_no: "A96110", country: "Uganda" }, // Present
      { access_no: "IN-202405", name: "Rahul Sharma", roll_no: "I95876", country: "India" }, // Present
      { access_no: "UG-202406", name: "Michael Ssebuguzi", roll_no: "A96001", country: "Uganda" }, // Present
      { access_no: "UG-202407", name: "Sharon Atwine", roll_no: "A96002", country: "Uganda" }, // Present
      { access_no: "UG-202408", name: "Henry Mukasa", roll_no: "A96003", country: "Uganda" }, // Present
      { access_no: "IN-202409", name: "Ananya Patel", roll_no: "I95877", country: "India" }, // Present
      { access_no: "UG-202410", name: "Joseph Kyambadde", roll_no: "A96004", country: "Uganda" }, // Absent
      { access_no: "UG-202411", name: "Faith Nakato", roll_no: "A96005", country: "Uganda" }, // Absent
      { access_no: "UG-202412", name: "David Odoch", roll_no: "A96006", country: "Uganda" }, // Absent
      { access_no: "UG-202413", name: "Esther Namiiro", roll_no: "A96007", country: "Uganda" }, // Absent
      { access_no: "IN-202414", name: "Vikram Singh", roll_no: "I95878", country: "India" }, // Absent
      { access_no: "IN-202415", name: "Pooja Verma", roll_no: "I95879", country: "India" }, // Absent
    ];

    // Mark 9 students as present
    const initialPresentStudents = dummyStudents
      .filter((student, index) => index < 9) // First 9 students present
      .map((student) => student.access_no);

    setStudents(dummyStudents);
    setPresentStudents(initialPresentStudents);

    LogBox.ignoreLogs(["Setting a timer"]); // Ignore warnings
  }, []);

  const handleSubmit = () => {
    const absentStudents = students
      .map((student) => student.access_no)
      .filter((access_no) => !presentStudents.includes(access_no));

    // Simulate saving attendance locally
    console.log("Present Students:", presentStudents);
    console.log("Absent Students:", absentStudents);

    Alert.alert("Success", "Attendance has been marked");
    navigation.navigate("Teacherhome"); // Redirect to home
  };

  return (
    <SafeAreaView style={styles.container}>
      <ScrollView>
        {students.map((student) => (
          <StudentBox
            key={student.access_no}
            setPresentStudents={setPresentStudents}
            presentStudents={presentStudents}
            item={student}
          />
        ))}

        <View style={styles.buttons}>
          <Button
            onPress={() => navigation.navigate("Teacherhome")}
            text="Cancel"
            icon={faTimes}
            color={colors.red}
            styles={styles}
          />
          <Button
            onPress={() => setShowModal(true)}
            text="Next"
            icon={faAngleDoubleRight}
            color={colors.primary}
            styles={styles}
          />
        </View>
      </ScrollView>

      <AttendanceModal
        handleSubmit={handleSubmit}
        presentStudents={presentStudents}
        visible={showModal}
        setShowModal={setShowModal}
        allStudentsId={students.map((student) => student.access_no)}
      />
    </SafeAreaView>
  );
};

export default Attendance;
